export function log(data){
    console.log(data)
}

/* 从内联样式 ial 中获取文章封面 
* data：文章数据
*/
export function getCover(data){

    if(!data || data.length == 0){ return data }else{
        for(let i=0; i< data.length; i++){
            let ial = data[i].ial
            let reg = /background-image:url\(assets\/(.*)\);{1}?/
            let img = ial.match(reg)
            if(img){
                let domain = "http://127.0.0.1:6806/assets/"
                data[i].cover = "background-image:url('" + domain + img[1] + "');"
            }else{
                let reg = /background-image:(.*?)["|;]/
                let img = ial.match(reg)
                if(img){
                data[i].cover = "background-image:" + img[1]
                }else{
                data[i].cover = null
                }
            }
        }
        return data

    }

    
}