<template>
<div id="table-view">
<el-table
    :data="data"
    border
    style="width: 100%">
    <el-table-column
      prop="content"
      label="标题"
      sortable
      min-width="150">
      <template #default="scope">
        <div class="title" @click="openNote(scope.row.id)"><span>{{scope.row.content}}</span></div>
      </template>
    </el-table-column>
    <el-table-column
      prop="new_created"
      label="创建日期"
      width="150">
    </el-table-column>
    <el-table-column
      prop="new_updated"
      label="更新日期"
      width="150">
    </el-table-column>
    <el-table-column
      prop="custom_lz_priority"
      label="优先级"
      :filters="[{ text: 'P0', value: 'P0' }, { text: 'P1', value: 'P1' }, { text: 'P2', value: 'P2' }, { text: 'P3', value: 'P3' }]"
      :filter-method="filterPriority"
      filter-placement="bottom-end"
      width="100">
      <template #default="scope">
        <el-tag type="success"  disable-transitions v-if="scope.row.custom_lz_priority">{{scope.row.custom_lz_priority}}</el-tag>
      </template>
    </el-table-column>
    <el-table-column
      prop="custom_lz_todo"
      label="待办"
      width="100"
      :filters="[{ text: 'doing', value: 'doing' }, { text: 'done', value: 'done' }]"
      :filter-method="filterTodo"
      filter-placement="bottom-end" >
      <template #default="scope">
        <el-tag type="primary"  disable-transitions v-if="scope.row.custom_lz_todo">{{scope.row.custom_lz_todo}}</el-tag>
      </template>
    </el-table-column>
    <el-table-column
      prop="memo"
      label="备注">
    </el-table-column>
  </el-table>
</div>
</template>

<script>
export default {
  props:{
    data: null,
  },
  methods:{
        openNote(id) { window.open("siyuan://blocks/" + id, "_blank")},

        filterPriority(value, row){
        return row.custom_lz_priority == value
        },
        
        filterTodo(value, row) {
          return row.custom_lz_todo === value;
        }
  },




}
</script>